<template>
  <div class="main">
    <img src="@/assets/img/me.jpg" alt="avatar" :class="{ play: isPlay }" @click="play" />
    <h1 class="name">Go Big or Go Home</h1>
    <nav>
      <router-link to="/blog">Blog</router-link>
      <a href="#" v-show="!isPlay" @click="play(true)">Play</a>
      <a href="#" v-show="isPlay" @click="play(false)">Stop</a>
      <a href="#" @click="tips">Work</a>
      <a href="/static/old/about">About</a>
    </nav>
    <div class="introduce hide-768">
      <div class="animated">
        <p style="font-size: 14px">- ABOUT ME -</p>
        <p>
          Like Writing Bugs
          <i class="fa fa-code"></i>,
        </p>
        <p>Like Movie, Like Singing.</p>
        <p>I'm Here Waiting For U~</p>
        <p>
          Resume
          <i class="fa fa-hand-o-right"></i>
          <a href="/static/resume.pdf" target="_blank">
            <i class="fa fa-drivers-license-o"></i>
          </a>
        </p>
        <p>
          My GitHub
          <i class="fa fa-hand-o-right"></i>
          <a href="https://github.com/asuperdog" target="_blank">
            <i class="fa fa-github"></i>
          </a>
        </p>
        <p>
          My Telegram
          <i class="fa fa-hand-o-right"></i>
          <a href="https://telegram.me/isuperdog" target="_blank">
            <i class="fa fa-weibo"></i>
          </a>
        </p>
        <p>
          Mail
          <i class="fa fa-hand-o-right"></i>
          <span class="gradient-text mail">bombiu@126.com</span>
        </p>
      </div>
    </div>
    <a href="https://beian.miit.gov.cn/" target="_blank" class="hide-768">粤ICP备19138440号</a>
    <audio id="music" type="audio/ogg" src />
  </div>
</template>

<script>
  export default {
    name: 'Home',
    data() {
      return {
        isPlay: false,
        musicList: [],
        currAudioSrcIndex: 0
      }
    },
    mounted() {
      this.getMusic()
    },
    methods: {
      getMusic() {
        this.$axios({
          method: 'get',
          url: 'static/music.json'
        }).then(res => {
          let music = res.data
          music.forEach((item, i) => {
            this.musicList.push(item.src)
          })
          this.init()
        }).catch(err => {
          console.log(err)
        })
      },
      init() {
        let arrLength = this.musicList.length
        let randomIndex = Math.floor(Math.random() * arrLength)
        this.currAudioSrcIndex = randomIndex
        this.audioElement = document.getElementById('music')
        this.audioElement.src = this.musicList[this.currAudioSrcIndex]
        console.log(this.audioElement.src)
        this.audioElement.addEventListener('ended', () => {
          if (this.currAudioSrcIndex === arrLength - 1) {
            this.isPlay = false
            this.currAudioSrcIndex === 0
            this.audioElement.src = this.musicList[0]
          } else {
            this.currAudioSrcIndex += 1
            this.audioElement.src = this.musicList[this.currAudioSrcIndex]
            this.audioElement.play()
          }
        })
      },
      play() {
        switch (this.isPlay) {
          case false:
            this.audioElement.play()
            break
          case true:
            this.audioElement.pause()
            break
        }
        this.isPlay = !this.isPlay
      },
      tips() {
        alert('项目正在重构中...')
      }
    }
  }
</script>

<style scoped>
  .main {
    user-select: none;
    text-align: center;
    margin-top: 120px;
    letter-spacing: 1px;
    font: 16px Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif;
  }

  .main>img {
    width: 90px;
    height: 90px;
    border-radius: 100%;
    display: inline-block;
  }

  .name {
    font-size: 14px;
    margin: 15px 15px 23px;
  }

  a {
    margin: 0 15px;
    font-size: 13px;
    display: inline-block;
    text-decoration: none;
    -webkit-text-decoration: none;
  }

  p {
    font-size: 13px;
  }

  .introduce {
    margin: 60px auto 70px;
  }

  .introduce p {
    margin-top: 15px;
  }

  .play {
    animation: rot 6s linear infinite;
  }

  @keyframes rot {
    form {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
</style>



