<template>
  <div class="container content">
    <div class="row">
      <div class="col-md-4">
        <div class="div-default div-avatar">
          <img src="@/assets/img/me.jpg" alt="avatar" class="avatar" />
        </div>
        <div class="div-default">
          <div class="title">标签</div>
          <div class="box">
            <span class="divider" @click="select('all')">全部</span>
            <span class="divider" v-for="(cat, index) in category" :key="index" v-text="cat.ptype"
              @click="select(cat.ptype)"></span>
          </div>
        </div>
        <div class="div-default hide-768">
          <div class="title">友情链接</div>
          <div class="box friendLink">
            <p>
              <a :href="friend.href" target="_blank" v-for="(friend, index) in friendType1" :key="index">
                <i class="fa" :class="friend.icotype == 0 ? 'fa-user' : 'fa-weibo'"></i>
                {{ friend.name }}
              </a>
            </p>
            <div class="line"></div>
            <p>
              <a :href="friend.href" target="_blank" v-for="(friend, index) in friendType0" :key="index">
                <i class="fa fa-user"></i>
                {{ friend.name }}
              </a>
            </p>
            <div class="gradient-text tips-links">欢迎邮件联系添加友链</div>
          </div>
        </div>
        <div class="div-default">
          <div class="title">导航栏</div>
          <div class="box">
            <router-link class="divider" to="/">返回首页</router-link>
            <router-link class="divider" to="/manage">登录后台</router-link>
          </div>
        </div>
      </div>
      <div class="col-md-8 div-article">
        <div class="div-default">
          <div class="title">博文</div>
          <div class="box">
            <div v-for="i in 6" v-show="loading">
              <skeleton type="text" line="1"></skeleton>
              <div class="line-skeleton"></div>
            </div>
            <article class="article" v-for="article in articles" :key="article.pid" v-show="!loading">
              <header>
                <h1>
                  <span class="a-title" v-text="article.ptitle" @click="detail(article.pid)"></span>
                </h1>
                <div class="a-time" v-text="article.ptime"></div>
              </header>
              <div class="a-subtitle" v-text="article.pkeywords"></div>
              <footer></footer>
            </article>
          </div>
          <div class="more" v-text="message" @click="loadMore"></div>
        </div>
      </div>
      <div class="col-md-3"></div>
    </div>
  </div>
</template>
<script>
  import skeleton from '@/components/skeleton';

  export default {
    name: 'Blog',
    components: {
      skeleton
    },
    data() {
      return {
        num: 0,
        article: [],
        articles: [],
        category: [],
        tempData: [],
        friendLink: [],
        loading: true,
        message: 'More'
      }
    },
    computed: {
      friendType0() {
        return this.friendLink.filter(friend => {
          return friend.type == 0
        })
      },
      friendType1() {
        return this.friendLink.filter(friend => {
          return friend.type == 1
        })
      }
    },

    //页面创建
    created() {
      this.getData()
    },

    methods: {
      //文章数据加载
      getData() {
        this.$axios({
          method: 'get',
          url: 'static/article.json'
        }).then(res => {
          this.article = this.isShow(res.data)
          this.tempData = this.handleLoad(this.isShow(res.data))
          setTimeout(() => {
            this.loading = false
          }, 800)
        }).catch(err => {
          console.log(err)
        })

        //友链数据加载
        this.$axios({
          method: 'get',
          url: 'static/friend.json'
        }).then(res => {
          this.friendLink = res.data
        }).catch(err => {
          console.log(err)
        })
        this.getCategory()
      },

      //分类数据加载
      getCategory() {
        this.$axios({
          method: 'get',
          url: 'static/category.json'
        }).then(res => {
          this.category = res.data
        }).catch(err => {
          console.log(err)
        })
      },

      //文章首屏数据隐藏处理
      isShow(arr) {
        let res = []
        arr.forEach((item, i) => {
          if (arr[i].pshow != 0) {
            res.push(arr[i])
          }
        })
        return res
      },

      //文章数据处理: 首次加载 6 条数据，然后点一次加载 5 条数据到后面
      handleLoad(arr) {
        let length = arr.length
        let arrNum = (length - 6) / 5
        let res = []
        this.articles = arr.splice(0, 6)
        if (arrNum > 0) {
          for (arrNum; arrNum >= 0; arrNum--) {
            res.push(arr.splice(0, 5))
          }
        }
        return res
      },

      //加载更多文章
      loadMore() {
        let nums = this.tempData.length
        if (this.num < nums) {
          this.articles = this.articles.concat(this.tempData[this.num])
          this.num++
        } else if (this.num == nums) {
          this.message = 'No More'
          setTimeout(() => {
            this.message = 'Close 😇'
          }, 500)
          this.num++
        } else {
          this.message = 'More'
          this.num = 0
          let id = setInterval(() => {
            if (this.articles.length > 6) {
              this.articles.pop()
            } else {
              clearInterval(id)
            }
          }, 16)
        }
      },

      //查看文章详情
      detail(id) {
        // let target = e.target || e.srcElement
        // let id = target.attributes['pid'].nodeValue
        this.$router.push({
          path: '/blog/passage/' + id
        })
      },

      //文章标签选择处理
      select(type) {
        let res = []
        let data = this.article
        if (type != 'all') {
          data.forEach((item, i) => {
            if (data[i].ptype === type) {
              res.push(data[i])
            }
          })
        } else {
          res = this.isShow(data)
        }
        this.tempData = this.handleLoad(res)
      }
    }
  }
</script>
<style scoped>
  /* 公用类 */
  p {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .line {
    border-bottom: 1px dashed #bbb;
  }

  .line-skeleton {
    margin: 24px 0 20px 0;
    border-bottom: 1px dashed #bbb;
  }

  .footer {
    margin-top: 10px;
    margin-bottom: 20px;
    text-align: center;
  }

  /* 头像 */
  .div-avatar {
    height: 180px;
    position: relative;
  }

  .avatar {
    top: 50%;
    left: 50%;
    width: 90px;
    position: absolute;
    border-radius: 100%;
    margin: -45px 0 0 -45px;
    transition-duration: 59s;
    transition-timing-function: cubic-bezier(0.34, 0, 0.84, 1);
  }

  .avatar:hover {
    transform: rotate(666turn);
  }

  /* 标签 */
  .divider {
    color: #2290bf;
    cursor: pointer;
    display: inline-block;
    padding-right: 10px;
    margin: 10px 10px 10px 0;
    border-right: 1px solid #999;
  }

  .divider:hover {
    color: #444;
  }

  /* 友链 */
  .friendLink a {
    color: #666;
    height: 30px;
    line-height: 30px;
    padding-right: 10px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
  }

  /*彩色文字*/
  .gradient-text {
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
  }

  .footer-text {
    font-weight: bold;
    letter-spacing: 0 !important;
    background-image: linear-gradient(to right, deeppink, dodgerblue);
  }

  .mail {
    background-image: linear-gradient(to right, #01579b, #6a1b9a);
  }

  .tips-links {
    font-size: 14px;
    background-image: linear-gradient(to right, #01579b, #6a1b9a);
  }

  /* 博文 */
  .div-article {
    padding: 0;
  }

  .div-article .article:nth-child(1) {
    margin-top: 5px;
  }

  .article {
    position: relative;
    min-height: 40px;
    margin: 0 0 10px;
    padding-top: 10px;
    border-bottom: 1px dashed #bbb;
  }

  header {
    margin: 0 0 12px;
  }

  h1 {
    font-size: 21px;
    font-weight: normal;
    margin: 0 0 2px 0;
  }

  .a-title {
    cursor: pointer;
  }

  .a-time {
    color: #bbb;
    font-size: 12px;
    /* font-style:italic; */
  }

  .a-subtitle {
    color: #666;
    cursor: default;
    overflow: hidden;
    font-weight: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  footer {
    margin-bottom: 15px;
  }

  .more {
    height: 42px;
    font-size: 14px;
    line-height: 42px;
    cursor: pointer;
    /* font-weight: bold; */
    text-align: center;
    background-image: linear-gradient(rgba(200, 200, 200, 0), rgba(200, 200, 200, 0.12));
  }

  .more:hover {
    background-image: linear-gradient(rgba(200, 200, 200, 0.03), rgba(200, 200, 200, 0.15));
  }

  /*媒体检测改变中框边距*/
  @media screen and (max-width: 768px) {

    .div-article,
    .col-md-3 {
      padding-left: 4%;
      padding-right: 4%;
    }

    .divider,
    a,
    .more {
      cursor: default;
    }
  }
</style>



