var Mock = require('mockjs')

article = Mock.mock({
  "array|5": [{
    pid: '@increment',
    pshow: 1,
    ptype: '',
    ptitle: '@word',
    ptime: '@datetime',
    pkeywords: '@csentence'
  }]
})

Mock.setup({
  timeout: '500-1000'
})

Mock.mock('/blog/getPassageList', 'get', getArticle)

function getArticle() {
  var res = {}
  var totalLength = article.array.length
  res.article = article.array
  res.length = totalLength
  res.status = 0
  return res
}



