<template>
  <div class="skeleton">
    <div class="text" v-if="type=='text'">
      <div class="line80"></div>
      <div class="line100"></div>
      <div class="line70"></div>
    </div>
    <div class="img" v-if="type=='img'">
      <div class="line100"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'skeleton',
    props: ['type', 'line']
  };
</script>

<style scoped>
  .skeleton>div>div {
    margin-bottom: 10px;
    background-color: #eee;
  }

  .text div {
    height: 15px;
  }

  .img div {
    height: 120px;
    margin-top: 20px;
  }

  .line80 {
    width: 80%;
  }

  .line100 {
    width: 100%;
  }

  .line70 {
    width: 70%;
  }
</style>



